/* Grid
–––––––––––––––––––––––––––––––––––––––––––––––––– */
.container {
  position: relative;
  width: 100%;
  max-width: 2000px;
  margin-right: auto;
  margin-left: auto;
  padding: 15px;
  box-sizing: border-box;
}
@media screen and (min-width: 1024px) {
  .container {
    padding: 75px;
  }
}

section,
.row {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  border: 1px solid red;
}

div,
.column {
  flex-basis: 100%;
  padding-top: 15px;
  padding-bottom: 15px;
  padding-left: 15px;
  padding-right: 15px;
}
@media screen and (min-width: 1024px) {
  div,
.column {
    flex: 1;
    padding-right: 75px;
  }
  div:last-child,
.column:last-child {
    padding-right: 0;
  }
  .p-column div,
.p-column .column {
    padding-top: 67.5px;
    padding-bottom: 67.5px;
    padding-left: 75px;
    padding-right: 75px;
  }
}

