/* Base Styles
–––––––––––––––––––––––––––––––––––––––––––––––––– */
/* NOTE
html is set to 62.5% so that all the REM measurements throughout Skeleton
are based on 10px sizing. So basically 1.5rem = 15px :) */
html {
  font-size: 62.5%;
}
body {
  min-height: 100vh;
  font-size: 1.5rem;
  line-height: 1.6;
  font-weight: 400;
  font-family: $bodyText;
  color: $baseTextColor;
}